  font-family: "Raleway", sans-serif;
}

body {
  height: 100vh;
  width: 100vw;
  margin: 0;
}

nav {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  background-color: #92E0E5;
  margin-left: 0;
  height: 62px;
}

.dog-icon {
  position: absolute;
  width: 40px;
  height: 40px;
  margin-left: 30px;
  margin-top: 13px;
  margin-bottom: 11px;
}

nav,
.intro-area,
.info-area,
.learn-more-area {
  width: 100vw;
}

main {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}

.nav-button {
  font-family: "Raleway", sans-serif;
  font-weight: 700;
  font-size: large;
  border: none;
  background-color: #92E0E5;
  margin-right: 20px;
  color: white;
}

button {
  color: #3F8DA8;
  border: 4px solid #3F8DA8;
  background-color: white;
  font-size: x-large;
  cursor: pointer;
}

#factsNav {
  margin-right: 40px;
}

.intro-area {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  height: 585px;
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
  background-repeat: no-repeat;
  background-size: 100%;
}
.intro-area .page-title {
  font-size: xx-large;
  font-weight: 500;
}

.page-title {
  margin-top: 47px;
}

.dog-intro-picture {
  border-radius: 50%;
  border: 5px solid #3F8DA8;
  height: 345px;
  margin-bottom: 20px;
}

.info-area {
  height: 655px;
  display: flex;
  flex-direction: row;
}

.info-card {
  width: 33%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}
.info-card h2 {
  margin-top: 50px;
  font-size: larger;
  font-weight: 500;
}
.info-card p {
  font-size: larger;
  text-align: center;
  margin-top: 50px;
  margin-left: 50px;
  margin-right: 50px;
  text-wrap: wrap;
}

.dog-info-picture {
  border-radius: 50%;
  border: 3px solid #3F8DA8;
  height: 240px;
  margin-top: 20px;
}

.learn-more-area {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  height: 245px;
  background-color: #3F8DA8;
  color: white;
}
.learn-more-area h3 {
  font-size: larger;
  font-weight: 500;
  margin-bottom: 50px;
}
.learn-more-area a {
  color: white;
}

.social-icons {
  display: flex;
  flex-direction: row;
  width: 500px;
  justify-content: space-between;
  margin-bottom: 30px;
}
.social-icons img {
  height: 55px;
  cursor: pointer;
